@import "/src/common/styles/variables.scss";

$height: 100px;

.Display {
  box-sizing: border-box;
  padding: $gutter-xl;
  width: 100%;
  height: 100px;
  border-radius: 5px;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  background-color: var(--screen-background);
  color: var(--text-primary-color);
  text-align: right;
}

.Display__currentInput {
  font-size: 50px;
  font-weight: bold;
  transform-origin: bottom right;
}

.Display__calculation {
  font-size: 0.5rem;
  position: absolute;
  right: $gutter-xl;
  top: 10px;
}
